import { useFormik } from 'formik';
import * as Yup from 'yup';

const Login = () => {
    const professions = ['Choose profession', 'Student', 'Employee', 'Employer'];
    const formik = useFormik({
        initialValues: {
          name: '',
          email: '',
          password: '',
          passwordcnf: '',
          profession: professions[0],
          age: '',
        },
        onSubmit: function (values) {
          alert(`You are registered! Name: ${values.name}. Email: ${values.email}. Profession: ${values.profession}. 
            Age: ${values.age}`);
        },
        validationSchema: Yup.object({
            name: Yup
                    .string()
                    .label('User Name')
                    .required()
                    .test('is-full-name', 'Please enter both your first and last name', function (value) {
                        const nameArr = value.split(" ");
                        return nameArr.length >= 2 && nameArr[0] && nameArr[1];
                      }),
            email: Yup
                    .string()
                    .email()
                    .required(),
            profession: Yup
                        .string()
                        .oneOf(professions, 'The profession you chose does not exist'),
            age: Yup
                  .number()
                  .min(15, 'You need to be older than 15 to register')
                  .required(),
            password: Yup
                  .string()
                  .min(10)
                  .required(),
            passwordcnf: Yup
                  .string()
                  .required()
                  .oneOf([Yup.ref('password'), null], 'Passwords must match')
          })
      })
    
      return (
        <div className="bg-blue-500 min-w-screen min-h-screen overflow-x-hidden">
          <form onSubmit={formik.handleSubmit} className="max-w-lg mx-auto bg-white rounded shadow-lg mt-7 p-3">
          <h1 className='text-3xl mb-3 text-center'>Register</h1>
            <div className='mb-4'>
              <label for="name">User Name</label>
              <input type="text" name="name" id="name" 
                className={`block w-full rounded border py-1 px-2 ${formik.touched.name && formik.errors.name ? 'border-red-400' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.name} />
              {formik.touched.name && formik.errors.name && (
                <span className='text-red-500'>{formik.errors.name}</span>
              )}
            </div>

            <div className='mb-4'>
              <label for="email">Email</label>
              <input type="email" name="email" id="email"
                className={`block w-full rounded border py-1 px-2 ${formik.touched.email && formik.errors.email ? 'border-red-400' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email} />
              {formik.touched.email && formik.errors.email && (
                <span className='text-red-500'>{formik.errors.email}</span>
              )}
            </div>

            <div className='mb-4'>
              <label for="profession">Profession</label>
              <select name="profession" id="profession"
                className={`block w-full rounded border py-1 px-2 ${formik.touched.profession && formik.errors.profession ? 'border-red-400' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.profession} >
                {professions.map((profession, index) => (
                  <option value={profession} key={index}>{profession}</option>
                ))}
              </select>
              {formik.touched.profession && formik.errors.profession && (
                <span className='text-red-500'>{formik.errors.profession}</span>
              )}
            </div>

            <div className='mb-4'>
              <label for="age">Age</label>
              <input type="number" name="age" id="age"
                className={`block w-full rounded border py-1 px-2 ${formik.touched.age && formik.errors.age ? 'border-red-500' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.age} />
              {formik.touched.age && formik.errors.age && (
                <span className='text-red-500'>{formik.errors.age}</span>
              )}
            </div>

            <div className='mb-4'>
              <label for="password">Password</label>
              <input type="password" name="password" id="password"
                className={`block w-full rounded border py-1 px-2 ${formik.touched.age && formik.errors.password ? 'border-red-500' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.password} />
              {formik.touched.password && formik.errors.password && (
                <span className='text-red-500'>{formik.errors.password}</span>
              )}
            </div>

            <div className='mb-4'>
              <label for="passwordcnf">Confirm Password</label>
              <input type="password" name="passwordcnf" id="passwordcnf"
                className={`block w-full rounded border py-1 px-2 ${formik.touched.passwordcnf && formik.errors.passwordcnf ? 'border-red-500' : 'border-gray-300'}`}
                onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.passwordcnf} />
              {formik.touched.passwordcnf && formik.errors.passwordcnf && (
                <span className='text-red-500'>{formik.errors.passwordcnf}</span>
              )}
            </div>

            <div className='text-center'>
              <button className='bg-blue-500 rounded p-3 text-white' type='submit'>Submit</button>
            </div>
          </form>
        </div>
      );
}
 
export default Login;